import React from 'react'
import TodoList from './component/TodoList'
import AddTodo from './component/AddTodo'
import Search from './component/Search'
import uuid from 'uuid'


export default class App extends React.Component {
    constructor() {
        super()
        this.state = {
            todos: [
                {
                    id: uuid.v4(),
                    title: 'ReactJs',
                    completed: false
                },
                {
                    id: uuid.v4(),
                    title: 'AngularJs',
                    completed: false
                },
                {
                    id: uuid.v4(),
                    title: 'HTML5',
                    completed: false
                }
            ],
            searchTodos: ''
        }
        this.isCompleted = this.isCompleted.bind(this)
        this.del = this.del.bind(this)
        this.add = this.add.bind(this)
        this.search = this.search.bind(this)
    }

    isCompleted(id) {
        this.setState({
            todos: this.state.todos.map(todo => {
                if (todo.id === id)
                    todo.completed = !todo.completed
                return todo;
            })
        })
    }

    del(id) {
        this.setState({
            todos: [...this.state.todos.filter(todo => todo.id !== id
            )],
            searchTodos: [...this.state.todos.filter(todo => todo.id !== id
            )]
        })
    }
    add(newItemTitle) {
        var newItem = {
            id: uuid.v4(),
            title: newItemTitle,
            completed: false
        }
        this.setState({
            searchTodos: [...this.state.todos, newItem],
            todos: [...this.state.todos, newItem]

        })
    }
    search(result) {
        if (result !== '')
            this.setState({
                searchTodos: result
            })
    }
    render() {
        return (
            <div className='container'>
                <h1 style={{
                    margin: '0 0 15px 0',
                    width: '100%',
                    color: 'snow',
                    textAlign: 'center'
                }}>Boring To Do List | ReactJs</h1>
                <AddTodo
                    add={this.add}
                />
                <Search
                    search={this.search}
                    todos={this.state.todos}
                />
                <TodoList
                    todos={this.state.todos}
                    searchTodos={this.state.searchTodos}
                    isCompleted={this.isCompleted}
                    del={this.del}
                />
            </div>
        )
    }
}

