import React from 'react'
import '../scss/Search.css'
export default class Search extends React.Component {
    constructor() {
        super()
        this.state = {
            search: ''
        }
        this.onChange = this.onChange.bind(this)
    }

    onChange(e) {
        var name = e.target.name;
        var value = e.target.value;

        this.setState({
            [name]: value
        })

        var current = this.props.todos;

        var newTodos = [];
        newTodos = current.filter(todo => {
            // to lower case title in todos and in search bar                
            const lc = todo.title.toLowerCase();
            const valueLc = value.toLowerCase();

            // return if 
            return lc.includes(valueLc) ? true : false;

        });

        this.props.search(newTodos);

    }

    render() {
        return (
            <input
                id='search-box'
                type='text'
                name='search'
                placeholder='search ...'
                value={this.state.search}
                onChange={this.onChange}
                onSubmit={this.onSubmit}
            />
        )
    }
}